import { useEffect, useState, useRef } from 'react'
import { AiOutlineLock } from 'react-icons/ai'
import { generateRandomKeyword } from '../../Signup/utils/apiFunctions'
import { RiDeleteBack2Line } from 'react-icons/ri'
import { message } from 'antd'
import { LoadingOutlined } from "@ant-design/icons";
import Token from '../../../../assets/img/token.png'
import VerificationInput from 'react-verification-input'

const TokenForm = ({ onSubmit, loading, setLoading, setToken, token, visibleError, errMessage, loginSubmit }) => {

    function botaoTimer() {
        let i = 45;

        document.getElementById('reenviar').textContent = `REENVIAR TOKEN EM 00:${i}`;
        document.getElementById('reenviar').disabled = true;

        let interval = setInterval(function () {
            i--;

            if (i < 10) {
                let reenvio = `REENVIAR TOKEN EM 00:0${i}`
                document.getElementById('reenviar').textContent = reenvio;
            } else {
                let reenvio = `REENVIAR TOKEN EM 00:${i}`
                document.getElementById('reenviar').textContent = reenvio;
            }
        }, 1000);

        setTimeout(function () {
            clearInterval(interval)
            document.getElementById('reenviar').textContent = "REENVIAR TOKEN";
            document.getElementById('reenviar').disabled = false;
        }, 45000);
    }

    useEffect(() => {
        botaoTimer()
    }, [])

    return (
        <>
            <div className="__signin_content_title">
                <span>Olá,</span>
                <span>Confirme o seu TOKEN enviado por email</span>
            </div>

            <div className='__signin_content_logo'>
                <img src={Token} className="__signin_content_logo_img" />
            </div>


                <VerificationInput
                    classNames={{
                        container: "__verifcation_container",
                        character: "__verification_character",
                        characterInactive: "__verification_character--inactive",
                        characterSelected: "__verification_character--selected",
                    }}
                    validChars="0-9"
                    inputProps={{type: "tel"}}
                    length={4}
                    onChange={(val) => setToken(val)}
                />

            {visibleError && (
                <div className='__signin_error'>
                    <span className='__signin_error_msg'>
                        {errMessage}
                    </span>
                </div>
            )}

            <div className="__signin_content_buttons">
                <button className="__signin_login" onClick={onSubmit} type="button">
                    {loading ? <LoadingOutlined /> : "Entrar"}
                </button>
                <div class="options_login">
                    <div class="border-login">
                        <div class="border-left"></div>
                    </div>
                    <div class="options_text">ou</div>
                    <div class="border-login">
                        <div class="border-right"></div>
                    </div>
                </div>
                <button className="__signin_cadastro" id="reenviar" onClick={loginSubmit}>Reenviar Token</button>
            </div>
        </>
    )
}

export default TokenForm