import { useState, useEffect } from "react";
import { useSelector } from "react-redux";

export const useAuth = () => {
    const user = useSelector((state) => state.authReducer.user);

    const [logged, setLogged] = useState(false);

    useEffect(() => {
        if (user) {
            setLogged(true)
        } else {
            setLogged(false)
        }
    }, [user]);

    console.log(logged)

    return logged;
};