import React, { useEffect, useState } from "react";
import './styles.css'
import logo_principal from '../../../assets/img/logo_myhart.png'
import { AiOutlineMail } from 'react-icons/ai'
import LoginForm from "./Form/LoginForm";
import PasswordForm from "./Form/PasswordForm";
import TokenForm from "./Form/TokenForm";
import { generateRandomKeyword, loginAccount, validateToken } from "../Signup/utils/apiFunctions";
import { message } from "antd";
import { useNavigate } from "react-router-dom";

// Redux
import { login } from "../../../slices/authSlice";
import { useDispatch, useSelector } from "react-redux";

const SignIn = () => {

    const [visibleError, setVisibleError] = useState(false)
    const [errMessage, setErrMessage] = useState('')
    const [step, setStep] = useState(0)
    const [teclado, setTeclado] = useState([])
    const [tecladoLoading, setTecladoLoading] = useState(false)
    const [exibicao, setExibicao] = useState([])
    const [senha, setSenha] = useState('')
    const [email, setEmail] = useState('')
    const [token, setToken] = useState('')
    const [loading, setLoading] = useState(false)
    const [toggle, setToggle] = useState(false)

    const navigate = useNavigate();
    const dispatch = useDispatch();

    async function generatePassword() {
        setTecladoLoading(true)

        let data = await generateRandomKeyword();

        // Transform in keyboard
        let keyData = [];
        for (let i = 0; i < 5; i++) {
            let valor = data.data.result[i];
            keyData.push(valor);
        }

        // Store in a state
        setTeclado(keyData)
        setExibicao(JSON.stringify(keyData))

        console.log("")
        setTecladoLoading(false)
    }

    async function handleNextStep() {
        if (email.length < 1) {
            setVisibleError(true)
            let err = "Digite um e-mail ou CPF"

            // Validations
            setErrMessage(err)

            console.log(errMessage)
            return 0;
        }

        generatePassword()
        setVisibleError(false)
        setErrMessage('')
        setStep(1)
    }

    function handlePreviousStep() {
        setStep(0)
        setEmail('')
        setVisibleError(false)
        setErrMessage('')
    }

    async function loginSubmit() {
        const data = {
            email: email,
            password: senha,
            keys: teclado
        }


        try {
            setLoading(true)

            let login = await loginAccount(data)

            if (login.data.error == true) {
                setVisibleError(true)
                let err = "Dados de acesso invalidos"
                setErrMessage(err)

                return 0;
            }

            if (senha.length < 6) {
                setVisibleError(true)
                let err = "A senha deve conter 6 digitos"
                setErrMessage(err)

                return 0;
            }

            setStep(2)
            setVisibleError(false)
            setErrMessage('')
            setLoading(false)
            message.success("Token reenviado com sucesso!")
        } catch (err) {

            if (err.response.data.message == "The password field is required.") {
                setVisibleError(true)
                let erro = "A senha é obrigatória"
                setErrMessage(erro)
            }

            if (err.response.data.message == "These credentials do not match our records.") {
                setVisibleError(true)
                let erro = "Dados de acesso invalidos"
                setErrMessage(erro)
            }

            if (err.response.data.message == "Account blocked for too many attempts") {
                setVisibleError(true)
                let erro = "Conta bloqueada"
                setErrMessage(erro)
            }

            
            if (err.response.data.message == "Account not validated") {
                setVisibleError(true)
                let erro = "Sua conta ainda não foi ativada"
                setErrMessage(erro)
            }

            setLoading(false)
        }
    }

    async function onSubmit() {
        const data = {
            email: email,
            password: senha,
            keys: teclado,
            token: token
        }

        try {
            setLoading(true)

            let res = await validateToken(data)

            if (res.data.error == true) {
                setVisibleError(true)
                let erro = "Token invalido"
                setErrMessage(erro)
                setLoading(false)
                return 0;
            }

            if (toggle == true) {
                localStorage.setItem('emailusu', email)
            }

            let tkUser = res.data.token
            dispatch(login(data))

            localStorage.setItem('tk-user', JSON.stringify(tkUser))

            navigate('/dashboard')
            setLoading(false)
            setErrMessage('')
            setVisibleError(false)
        } catch (err) {
            message.err("Token invalido")
        }
        setLoading(false)
    }

    return (
        <div className="__signin_container">
            <div className="__signin_content">
                <div className="__signin_padding">
                    <div className="__signin_header">
                        <img src={logo_principal} alt="Logo principal MyHart" width="350px" />
                    </div>
                    <section>
                        <div className="__signin_content_form">
                            <form onSubmit={e => { e.preventDefault(); }}>
                                <div className="form_group_signin">
                                    <article>
                                        {step == 0 && (
                                            <LoginForm
                                                setEmail={setEmail}
                                                handleNextStep={handleNextStep}
                                                loading={loading}
                                                setLoading={setLoading}
                                                errMessage={errMessage}
                                                setErrMessage={setErrMessage}
                                                visibleError={visibleError}
                                                toggle={toggle}
                                                setToggle={setToggle}
                                            />
                                        )}
                                        {step == 1 && (
                                            <PasswordForm
                                                onSubmit={loginSubmit}
                                                senha={senha}
                                                setSenha={setSenha}
                                                setExibicao={setExibicao}
                                                exibicao={exibicao}
                                                teclado={teclado}
                                                loginSubmit={loginSubmit}
                                                handlePreviousStep={handlePreviousStep}
                                                loading={loading}
                                                setLoading={setLoading}
                                                tecladoLoading={tecladoLoading}
                                                setTecladoLoading={setTecladoLoading}
                                                errMessage={errMessage}
                                                visibleError={visibleError}
                                            />
                                        )}
                                        {step == 2 && (
                                            <TokenForm
                                                onSubmit={onSubmit}
                                                loginSubmit={loginSubmit}
                                                senha={senha}
                                                setSenha={setSenha}
                                                setExibicao={setExibicao}
                                                setToken={setToken}
                                                token={token}
                                                exibicao={exibicao}
                                                teclado={teclado}
                                                handlePreviousStep={handlePreviousStep}
                                                loading={loading}
                                                setLoading={setLoading}
                                                tecladoLoading={tecladoLoading}
                                                setTecladoLoading={setTecladoLoading}
                                                errMessage={errMessage}
                                                visibleError={visibleError}
                                            />
                                        )}
                                    </article>
                                </div>
                            </form>
                        </div>
                    </section>
                </div>
            </div>
        </div>
    )
}

export default SignIn;